import { useState } from "react";
import { useRouter } from "next/router";
import {
  Box,
  Button,
  Flex,
  Stack,
  Heading,
  Text,
  Input,
  IconButton,
  HStack,
  Link as ChakraLink,
  Separator,
  Field,
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { LuEye, LuEyeOff, LuUser, LuMail, LuLock, LuGithub } from "react-icons/lu";
import { FaGoogle } from "react-icons/fa";

export default function SignUpPage() {
  const [showPassword, setShowPassword] = useState(false);
  const router = useRouter();

  const handleSignUp = (e: React.FormEvent) => {
    e.preventDefault();
    // Implementation logic goes here
    alert("Signing up...");
  };

  return (
    <Flex
      minH="100vh"
      align="center"
      justify="center"
      bg="gray.50"
      _dark={{ bg: "gray.950" }}
      bgGradient="to-br"
      gradientFrom="teal.50"
      gradientTo="gray.50"
      _darkGradientFrom="gray.950"
      _darkGradientTo="teal.950"
      p={4}
    >
      <motion.div
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.4, ease: "easeOut" }}
        style={{ width: "100%", maxWidth: "450px" }}
      >
        <Box 
          p={{ base: 6, md: 10 }} 
          bg="white" 
          _dark={{ bg: "gray.900" }} 
          boxShadow="2xl" 
          borderRadius="3xl"
          borderWidth="1px"
          borderColor="gray.100"
          _darkBorderColor="gray.800"
        >
          <Stack gap={8}>
            <Stack align="center" textAlign="center" gap={2}>
              <Heading size="3xl" fontWeight="black" letterSpacing="tight">
                Get Started
              </Heading>
              <Text color="gray.500" fontSize="md">
                Create your account to start managing your data.
              </Text>
            </Stack>

            <Stack gap={4}>
              <Button variant="outline" w="full" size="lg" gap={3}>
                <FaGoogle color="#EA4335" /> Sign up with Google
              </Button>
              <Button variant="outline" w="full" size="lg" gap={3}>
                <LuGithub /> Sign up with GitHub
              </Button>
            </Stack>

            <HStack w="full">
              <Separator flex="1" />
              <Text fontSize="xs" color="gray.400" whiteSpace="nowrap" textTransform="uppercase" letterSpacing="widest">
                Or use email
              </Text>
              <Separator flex="1" />
            </HStack>

            <Box as="form" onSubmit={handleSignUp}>
              <Stack gap={5}>
                <Field.Root required>
                  <Field.Label fontSize="sm" fontWeight="bold">Full Name</Field.Label>
                  <Input 
                    placeholder="John Doe" 
                    size="lg" 
                    variant="subtle" 
                    _focus={{ bg: "white", borderColor: "teal.500" }}
                  />
                </Field.Root>

                <Field.Root required>
                  <Field.Label fontSize="sm" fontWeight="bold">Email Address</Field.Label>
                  <Input 
                    type="email" 
                    placeholder="john@example.com" 
                    size="lg" 
                    variant="subtle"
                  />
                </Field.Root>

                <Field.Root required>
                  <Field.Label fontSize="sm" fontWeight="bold">Password</Field.Label>
                  <Box position="relative">
                    <Input
                      type={showPassword ? "text" : "password"}
                      placeholder="••••••••"
                      size="lg"
                      variant="subtle"
                      pe="12"
                    />
                    <IconButton
                      aria-label="Toggle password"
                      variant="ghost"
                      position="absolute"
                      right="1"
                      top="50%"
                      transform="translateY(-50%)"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <LuEyeOff /> : <LuEye />}
                    </IconButton>
                  </Box>
                  <Field.HelperText fontSize="xs">At least 8 characters long.</Field.HelperText>
                </Field.Root>

                <Button 
                  type="submit" 
                  colorPalette="teal" 
                  size="xl" 
                  w="full" 
                  fontWeight="bold"
                  boxShadow="0px 10px 20px -5px rgba(49, 151, 149, 0.4)"
                  _hover={{ transform: "translateY(-2px)", boxShadow: "0px 15px 25px -5px rgba(49, 151, 149, 0.5)" }}
                  transition="all 0.2s"
                >
                  Create Account
                </Button>
              </Stack>
            </Box>

            <Text textAlign="center" fontSize="sm" color="gray.500">
              Already have an account?{" "}
              <ChakraLink 
                color="teal.600" 
                fontWeight="bold" 
                textDecoration="none"
                _hover={{ textDecoration: "underline" }}
                onClick={() => router.push("/login")}
              >
                Sign in
              </ChakraLink>
            </Text>
          </Stack>
        </Box>
      </motion.div>
    </Flex>
  );
}